.wrapper_heading {
    margin: 2em 0 1em 0;
    padding: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    h2 {
        text-align: center;
        font-size: 40px;
        font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-18)/var(--unnamed-line-spacing-30) var(--unnamed-font-family-montserrat);
        letter-spacing: var(--unnamed-character-spacing-0-27);
        text-align: center;
        font: normal normal 600 40px/30px Montserrat;
        margin: 0 0 0.5em 0; }

    p {
        font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-18)/var(--unnamed-line-spacing-30) var(--unnamed-font-family-montserrat);
        letter-spacing: var(--unnamed-character-spacing-0-27);
        text-align: center;
        font: normal normal normal 18px/30px Montserrat;
        letter-spacing: 0.27px;
        color: #696969;
        width: 80%; } }

.row {
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto;
    grid-column-gap: 20px;
    justify-items: center;
    align-items: center;
    @media screen and (max-width: 920px) {
        grid-template-columns: auto; }

    .card {
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        margin: 1em 0;
        width: 314px;
        height: 450px !important;
        border-radius: 50px;
        background: #fff !important;

        .card-img-top {
            width: 314px;
            height: 200px;
            border-radius: 20px 20px 0 0; }

        .card-img-top:hover {
            background: #515 !important; }

        .card-body {
            width: 314px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            h3 {
                font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-35)/45px var(--unnamed-font-family-cormorant-garamond);
                color: var(--unnamed-color-161616);
                text-align: center;
                font: normal normal bold 32px/45px Cormorant Garamond !important;
                letter-spacing: -0.35px;
                color: #161616; }
            h6 {

                text-align: center;
                letter-spacing: 0.9px;
                color: #898989;
                text-transform: uppercase;
                margin-bottom: 1em;
                font-family: 'Montserrat';
                font-size: 14px !important;
                font-weight: 600; }

            p {

                text-align: center;
                letter-spacing: 0.24px;
                color: #898989;
                font-family: 'Montserrat';
                font-size: 12px !important;
                font-weight: 500;
                button {
                    outline: none;
                    border: none;
                    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) 16px/29px var(--unnamed-font-family-montserrat);
                    color: var(--unnamed-color-2c2c2c);
                    text-align: center;
                    text-decoration: underline;
                    font: normal normal 600 15px/22px Montserrat;
                    letter-spacing: 0.24px;
                    color: #2C2C2C; } } }

        .card-button {
            button {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0 0.6em !important;
                margin: 0 auto;
                width: 314px;
                height: 62px;
                outline: none;
                border: 0;
                cursor: pointer;
                border-radius: 0 0 20px 20px;
                // background: #53BFD2
                text-transform: uppercase;
                font-weight: bold;
                color: #fff;
                p {
                    margin: 0;
                    text-transform: lowercase;
                    display: flex;
                    align-items: center;
                    transition: .2s ease-in-out;

                    font: italic normal 300 15px sans-sarif; }

                img {
                    position: absolute;
                    left: 50%;
                    width: 40px; }
                span {
                    margin-left: 0.3em;
                    font: normal normal 300 16px sans-sarif; }

                // &:hover
 } } } }                //     background: red
.showmore {
    display: flex;
    justify-content: center;
    margin: 1em 0 3em 0;
    padding: 0 1em;

    .showmore_btn {
        outline: none;
        border: none;
        padding: 0 1em;
        text-transform: uppercase;
        font-weight: bold;
        img {
            margin: 0 0 0 1em; } } }

.readmore {
    margin: 0 0.5em;
    text-decoration: underline;
    transition: .2s ease-in-out;
    &:hover {
        color: #53BFD2; } }

.no_match {
    color: #53BFD2;

    text-align: center;
    font-size: 40px;
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-18)/var(--unnamed-line-spacing-30) var(--unnamed-font-family-montserrat);
    letter-spacing: var(--unnamed-character-spacing-0-27);
    text-align: center;
    font: normal normal 600 40px/30px Montserrat;
    margin: 2em 0 0em 0; }
